import {
  SHOW_LOGIN,
  HIDE_LOGIN,
  TOGGLE_MODE,
  TOGGLE_MODE_TEXT,
  TO_SUN,
  TO_YUE,
  LOGIN_STATE,
  ACCOUNT_UID,
  SET_LOAD,
  RETURN_LOAD,
  SET_LINK_PAGE,
  SET_PLAY_SATE,
  SET_FULL_SCREEN,
  SET_AUDIO_LIST,
  SET_AUDIO_INDEX,
  SET_PLAY_LIST,
  SET_AUDIO_MODE,
  SET_PLAYING_SHOW,
  SET_LEVEL,
  SET_USING_ALBUM_ID
} from './mutation-types'

// 
import ModalHelper from 'utils/modalScroll'

export default {
  // 
  [SHOW_LOGIN] (state) {
    state.loginPage = true
    ModalHelper.afterOpen()
  },
  // 
  [HIDE_LOGIN] (state) {
    state.loginPage = false
    ModalHelper.beforeClose()
  },
  // 
  [TO_YUE] (state) {
    console.log('')
    state.iconyueliang1 = false
    state.icontaiyang = true
    this._mutations.TOGGLE_MODE_TEXT[0](state)
  },
  // 
  [TO_SUN] (state) {
    console.log('')
    state.iconyueliang1 = true
    state.icontaiyang = false
    this._mutations.TOGGLE_MODE_TEXT[0](state)
  },
  //  
  [TOGGLE_MODE] (state) {
    console.log(this)
    if (state.iconyueliang1) {
      this._mutations.TO_YUE[0](state)
    } else if (state.icontaiyang) {
      this._mutations.TO_SUN[0](state)
    }
  },
  //  
  [TOGGLE_MODE_TEXT] (state) {
    if (state.icontaiyang) {
      state.modeText = ''
    } else {
      state.modeText = ''
    }
  },
  // 
  [LOGIN_STATE] (state, num) {
    state.loginState = num
  },
  [SET_LEVEL] (state, num) {
    state.level = num
  },
  //  uid
  [ACCOUNT_UID] (state, id) {
    state.accountUid = id
  },
  // loading
  [SET_LOAD] (state) {
    state.load = false
  },
  [RETURN_LOAD] (state) {
    state.load = true
  },
  /**
   * 
   */
  [SET_LINK_PAGE] (state, page) {
    state.linkPage = page
  },
  /**
   * 
   * @param {*} state state 
   * @param {*} flag 
   */
  [SET_PLAY_SATE] (state, flag) {
    state.playState = flag
  },
  /**
   * 
   */
  [SET_FULL_SCREEN] (state, flag) {
    state.fullScreen = flag
  },
  /**
   * 
   */
  [SET_AUDIO_LIST] (state, list) {
    state.audioList = list
  },
  /**
   * 
   */
  [SET_AUDIO_INDEX] (state, index) {
    state.audioIngIndex = index
  },
  /**
   * 
   */
  [SET_PLAY_LIST] (state, list) {
    state.playList = list
  },
  /**
   * 
   * @param {*} state state
   * @param {*} mode 
   */
  [SET_AUDIO_MODE] (state, mode) {
    state.mode = mode
  },
  /**
   * 
   */
  [SET_PLAYING_SHOW] (state, boolean) {
    state.playingShow = boolean
  },
  [SET_USING_ALBUM_ID] (state, albumID) {
    state.albumId = albumID
  }
}
