<template>
  <div class="wrapper"
       ref="lyric">
    <div class="full"
         v-if="noLyric">{{noLyricText}}</div>
    <ul :style="{marginTop: marginTop}"
        v-if="!noLyric">
      <li v-for="(item, index) in lyricArray"
          :key="index"
          :class="{active: index === nowLyricIndex}">{{ item | setWords }}</li>
    </ul>
  </div>
</template>

<script>
const REM = document.body.clientWidth * 0.14
export default {
  name: '',
  props: {
    lyricArray: {
      type: Array
    },
    // 
    nowLyricIndex: {
      type: Number
    },
    noLyric: {
      type: Boolean
    },
    noLyricText: {
      type: String
    }
  },
  data () {
    return {
      marginTop: '0rem'
    }
  },
  filters: {
    setWords: function (val) {
      if (val && val.words) {
        return val.words
      }
    }
  },
  methods: {
    setScroll (index) {
      //  8.3 rem
      //   / 2 -  li  / 2
      //  0.6 rem
      let top = index * 0.6 * REM
      // this.marginTop = top + 'rem'
      this.$refs.lyric.scroll({
        top,
        left: 0,
        behavior: 'smooth' //  smooth(),instant(),auto
      })
    }
  }
}
</script>

<style lang='less' scoped>
@import url("~styles/global.less");

.wrapper {
  height: 7.6rem;
  // box-sizing: border-box;
  margin: 0.6rem 0;
  color: #ccc;
  overflow: scroll;
  ul {
    margin: 0;
    padding: 0;
    padding-top: 3.5rem;
    list-style: none;
    text-align: center;
    /* ulmargin-top0.7 */
    transition: margin-top 0.7s;
    li {
      height: 0.6rem;
      line-height: 1.5;
      .ellipsis();
      &.active {
        color: #fff;
      }
    }
  }
  .full {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
</style>
