export const getRandomArrayElements = (arr, count) => {
  // eslint-disable-next-line one-var
  let shuffled = arr.slice(0),
    // 
    i = arr.length,
    temp,
    index
  //   
  //  
  while (i > 0) {
    // 
    index = Math.floor(i-- * Math.random())
    // 
    // 
    temp = shuffled[index]
    shuffled[index] = shuffled[i]
    shuffled[i] = temp
  }
  return shuffled.slice(0, count)
}
